import React from "react";
import "./neomarphismstyle.css";
import CountUp from "react-countup";

const CountAnim = props => {
  return (
    <CountUp start={0} end={props.data} delay={0}>
      {({ countUpRef }) => <strong ref={countUpRef}></strong>}
    </CountUp>
  );
};

export const Neomarphismui = props => {
  return (
    <section>
      <h4>{props.title}</h4>
      <div className={props.color}>
        <span className={props.size ? props.size : ""}>
          {props.noAnime ? (
            <strong>{props.data}</strong>
          ) : (
            <CountAnim data={props.data} />
          )}
        </span>
        {props.noAnime ? (
          <span></span>
        ) : (
          <span>{isNaN(props.inc) ? 0 : props.inc}</span>
        )}
      </div>
    </section>
  );
};
