import React, { useState, useEffect, useRef } from "react";
import * as d3 from "d3";
import { legendColor } from "d3-svg-legend";
import * as topojson from "topojson";
import { Neomarphismui } from "../../Neomorphism";
import { Container, Row, Col } from "react-bootstrap";
import indiaMapJson from "./india.json";

function ChoroplethMap(props) {
  const [rendered, setRendered] = useState(false);
  const [states, setStates] = useState(props.states);
  const [state, setState] = useState({});
  const [statistic, setStatistic] = useState({});
  const [index, setIndex] = useState(1);
  const choroplethMap = useRef(null);

  useEffect(() => {
    if (props.states.length > 1 && choroplethMap.current) {
      mapData(choroplethMap.current);
      setState(states[1]);
    }
  }, [statistic]);

  useEffect(() => {
    if (states.length > 1) {
      let total = 0;
      let maxConfirmed = parseInt(states[1].confirmed);
      let minConfirmed = parseInt(states[1].confirmed);
      for (let i = 1; i < states.length; i++) {
        total += parseInt(states[i].confirmed);
        if (parseInt(states[i].confirmed) > parseInt(maxConfirmed))
          maxConfirmed = parseInt(states[i].confirmed);
        if (parseInt(states[i].confirmed) < parseInt(minConfirmed))
          minConfirmed = parseInt(states[i].confirmed);
      }
      setStatistic({
        total: total,
        maxConfirmed: maxConfirmed,
        minConfirmed: minConfirmed
      });
    }
  }, [states.length]);

  useEffect(() => {
    setStates(props.states);
  }, [props.states]);

  const handleMouseover = name => {
    states.map((state, index) => {
      if (state.state.toLowerCase() === name.toLowerCase()) {
        setState(state);
        setIndex(index);
      }
    });
  };

  const mapData = selector => {
    const svg = d3.select(selector);
    const width = +svg.attr("width");
    const height = +svg.attr("height");

    const unemployment = d3.map();

    const projection = d3
      .geoMercator()
      .center([78.9, 19])
      .scale(1000)
      .translate([width / 2, height / 2]);

    const path = d3.geoPath(projection);

    // Colorbar
    const maxInterpolation = 0.8;

    function label({ i, genLength, generatedLabels, labelDelimiter }) {
      if (i === genLength - 1) {
        const n = Math.floor(generatedLabels[i]);
        return `${n}+`;
      } else {
        const n1 = Math.floor(generatedLabels[i]);
        const n2 = Math.floor(generatedLabels[i + 1]);
        return `${n1} - ${n2}`;
      }
    }

    const color = d3
      .scaleSequential(d3.interpolateReds)
      .domain([0, statistic.maxConfirmed / maxInterpolation]);

    svg
      .append("g")
      .attr("class", "legendLinear")
      .attr("transform", "translate(373, 278)");

    const numCells = 6;
    const delta = Math.floor(statistic.maxConfirmed / (numCells - 1));
    const cells = Array.from(Array(numCells).keys()).map(i => i * delta);

    const legendLinear = legendColor()
      .shapeWidth(50)
      .cells(cells)
      .titleWidth(3)
      .labels(label)
      .title("Confirmed Cases")
      .orient("vertical")
      .scale(color);

    svg.select(".legendLinear").call(legendLinear);

    const promises = [JSON.parse(JSON.stringify(indiaMapJson))];

    Promise.all(promises).then(ready);

    function ready([india]) {
      states.map((state, index) => {
        unemployment.set(state.state.toLowerCase(), state.confirmed);
      });

      svg
        .append("g")
        .attr("class", "states")
        .selectAll("path")
        .data(topojson.feature(india, india.objects.india).features)
        .enter()
        .append("path")
        .attr("fill", function(d) {
          const n = unemployment.get(d.properties.ST_NM.toLowerCase());
          return d3.interpolateReds(
            (d.confirmed =
              (n > 0) * 0.05 + (n / statistic.maxConfirmed) * maxInterpolation)
          );
        })
        .attr("d", path)
        .attr("pointer-events", "all")
        .on("mouseenter", d => {
          handleMouseover(d.properties.ST_NM);
          const target = d3.event.target;
          d3.select(target.parentNode.appendChild(target))
            .attr("stroke", "#ff073a")
            .attr("stroke-width", 2);
        })
        .on("mouseleave", d => {
          const n = unemployment.get(d.properties.ST_NM.toLowerCase());
          const target = d3.event.target;
          d3.select(target)
            .attr(
              "fill",
              d3.interpolateReds(
                (d.confirmed =
                  (n > 0) * 0.05 +
                  (n / statistic.maxConfirmed) * maxInterpolation)
              )
            )
            .attr("stroke", "None");
        })
        .style("cursor", "pointer")
        .append("title")
        .text(function(d) {
          return (
            parseFloat(
              100 *
                (unemployment.get(d.properties.ST_NM.toLowerCase()) /
                  statistic.total)
            ).toFixed(2) +
            "% from " +
            toTitleCase(d.properties.ST_NM)
          );
        });

      svg
        .append("path")
        .attr("stroke", "#ff073a20")
        .attr("fill", "none")
        .attr("stroke-width", 2)
        .attr("d", path(topojson.mesh(india, india.objects.india)));
    }
  };

  const toTitleCase = str => {
    str = str.toLowerCase().split(" ");
    for (let i = 0; i < str.length; i++) {
      str[i] = str[i].charAt(0).toUpperCase() + str[i].slice(1);
    }
    return str.join(" ");
  };

  return (
    <React.Fragment>
      <Col xs={12} className="text-center">
        <h1 className="header">Statistics by State</h1>
      </Col>
      <Col xs={12} className="text-center">
        <h6 className="header">
          {window.innerWidth <= 769 ? "Tap" : "Hover"} over a state for more
          details
        </h6>
      </Col>
      <Col xs={12} md={6} lg={6} sm={12}>
        <Row>
          <Col>
            <Neomarphismui
              title="State"
              color="neumorphic variation2 blue"
              data={state.state}
              size="small"
              noAnime
            />
          </Col>
          <Col>
            <Neomarphismui
              title="Confirmed"
              color="neumorphic variation2 brown"
              data={state.confirmed}
              noAnime
            />
          </Col>

          <Col>
            <Neomarphismui
              title="Active"
              color="neumorphic variation2 orange"
              data={state.active}
              noAnime
            />
          </Col>
          <Col>
            <Neomarphismui
              title="Recovered"
              color="neumorphic variation2 green"
              data={state.recovered}
              noAnime
            />
          </Col>
          <Col>
            <Neomarphismui
              title="Deceased"
              color="neumorphic variation2 red"
              data={state.deaths}
              noAnime
            />
          </Col>
        </Row>
      </Col>
      <Col xs={12} md={6} lg={6} sm={12}>
        <svg
          id="chart"
          width={`556`}
          height={`400`}
          viewBox={`450 -200 275 739`}
          preserveAspectRatio="xMidYMid meet"
          ref={choroplethMap}
        ></svg>
      </Col>
    </React.Fragment>
    // <div
    //   className="ChoroplethMap fadeInUp"
    //   style={{
    //     animationDelay: "1.2s",
    //     position: "relative"
    //   }}
    // >
    //   <h1 className="header">Statistics by State</h1>
    //   <h6 className="header">
    //     {window.innerWidth <= 769 ? "Tap" : "Hover"} over a state for more
    //     details
    //   </h6>
    //   <div className="svg-parent" style={{ textAlign: "end" }}>
    //     <svg
    //       id="chart"
    //       width="650"
    //       height={window.innerWidth <= 479 ? 650 : 750}
    //       viewBox={`0 0 650 ${window.innerWidth <= 479 ? 650 : 750}`}
    //       preserveAspectRatio="xMidYMid meet"
    //       ref={choroplethMap}
    //     ></svg>
    //   </div>

    //   <div
    //     className="map-stats"
    //     style={{
    //       display: "grid",
    //       gridTemplateColumns: "repeat(auto-fit, minmax(165px, 1fr))",
    //       width: "40%",
    //       position: "absolute",
    //       top: "8rem",
    //       left: "1px"
    //     }}
    //   >
    //     <Neomarphismui
    //       title="State"
    //       color="neumorphic variation2 blue"
    //       data={state.state}
    //       size="small"
    //     />

    //     <Neomarphismui
    //       title="Confirmed"
    //       color="neumorphic variation2 brown"
    //       data={state.confirmed}
    //     />
    //     <Neomarphismui
    //       title="Active"
    //       color="neumorphic variation2 orange"
    //       data={state.active}
    //     />
    //     <Neomarphismui
    //       title="Recovered"
    //       color="neumorphic variation2 green"
    //       data={state.recovered}
    //     />
    //     <Neomarphismui
    //       title="Deceased"
    //       color="neumorphic variation2 red"
    //       data={state.deaths}
    //     />
    //   </div>
    // </div>
  );
}

export default ChoroplethMap;
