import React, { useState, useEffect } from "react";
import style from "./style.css";
import axios from "axios";
import RenderLineChart from "../charts/linechart";
import BarChartGraph from "../charts/barchart";
import ChoroplethMap from "../charts/map/choropleth";
import { Container, Row, Col } from "react-bootstrap";
import Stats from "../stats";

const DashBoard = props => {
  const [states, setStates] = useState([]);
  const [total, setTotal] = useState(0);
  const [fetched, setFetched] = useState(false);
  const [lastUpdated, setLastUpdated] = useState("");
  const [timeseries, setTimeseries] = useState([]);
  const [deltas, setDeltas] = useState([]);
  useEffect(() => {
    if (fetched === false) {
      getStates();
    }
  }, [fetched]);
  const getStates = () => {
    axios
      .get("https://api.covid19india.org/data.json")
      .then(response => {
        // console.log(
        //   "irfan",
        //   response.data.cases_time_series,
        //   response.data.key_values[0]
        // );

        setTotal(response.data.statewise[0].confirmed);

        //console.log("response.data.statewise ", response.data.statewise);
        setStates(response.data.statewise);
        // response.data.cases_time_series.length =
        //   response.data.cases_time_series.length - 1;
        setTimeseries(response.data.cases_time_series);
        setLastUpdated(
          response.data.statewise[0].lastupdatedtime.slice(0, 15) +
            response.data.statewise[0].lastupdatedtime.slice(18)
        );
        setDeltas(response.data.key_values[0]);
        setFetched(true);
      })
      .catch(err => {
        console.log(err);
      });
  };
  return (
    <Container>
      <Stats data={states} deltas={deltas} />
      <Row>
        <Col xs={12} sm={12} md={6} lg={6}>
          {timeseries.length > 0 ? (
            <div className="card">
              <RenderLineChart timeseries={timeseries} total={total} />
            </div>
          ) : (
            "Loading..."
          )}
        </Col>
        <Col xs={12} sm={12} md={6} lg={6}>
          {states.length > 0 ? (
            <div className="card">
              <BarChartGraph states={states} />
            </div>
          ) : (
            "Loading..."
          )}
        </Col>
      </Row>
      <Row>
        <ChoroplethMap states={states} />
      </Row>
    </Container>
  );
};

export default DashBoard;
