import React, { useState, useEffect } from "react";
import { Row, Col } from "react-bootstrap";
import { Neomarphismui } from "../Neomorphism";

const Stats = props => {
  const [data, setData] = useState(props.data);
  const [confirmed, setConfirmed] = useState(0);
  const [active, setActive] = useState(0);
  const [recoveries, setRecoveries] = useState(0);
  const [deaths, setDeaths] = useState(0);

  useEffect(() => {
    setData(props.data);
    parseData();
  });

  const parseData = () => {
    let confirmed = 0;
    let active = 0;
    let recoveries = 0;
    let deaths = 0;
    data.map((state, index) => {
      if (index !== 0) {
        confirmed += parseInt(state.confirmed);
        active += parseInt(state.active);
        recoveries += parseInt(state.recovered);
        deaths += parseInt(state.deaths);
      }
    });
    setConfirmed(confirmed);
    setActive(active);
    setRecoveries(recoveries);
    setDeaths(deaths);
  };
  return (
    <Row>
      <Col xs={6} sm={6} md={3} lg={3}>
        <Neomarphismui
          title="Confirmed"
          color="neumorphic variation2 brown"
          data={confirmed}
          inc={
            props.deltas
              ? props.deltas.confirmeddelta >= 0
                ? "+" + props.deltas.confirmeddelta
                : props.deltas.confirmeddelta
              : ""
          }
        />
      </Col>
      <Col xs={6} sm={6} md={3} lg={3}>
        <Neomarphismui
          title="Active"
          color="neumorphic variation2 orange"
          data={active}
          inc={
            props.deltas
              ? props.deltas.confirmeddelta -
                  props.deltas.recovereddelta -
                  props.deltas.deceaseddelta >=
                0
                ? "+" +
                  (
                    props.deltas.confirmeddelta -
                    props.deltas.recovereddelta -
                    props.deltas.deceaseddelta
                  ).toString()
                : props.deltas.confirmeddelta -
                  props.deltas.recovereddelta -
                  props.deltas.deceaseddelta
              : ""
          }
        />
      </Col>
      <Col xs={6} sm={6} md={3} lg={3}>
        <Neomarphismui
          title="Recovered"
          color="neumorphic variation2 green"
          data={recoveries}
          inc={
            props.deltas
              ? props.deltas.recovereddelta >= 0
                ? "+" + props.deltas.recovereddelta
                : props.deltas.recovereddelta
              : ""
          }
        />
      </Col>
      <Col xs={6} sm={6} md={3} lg={3}>
        <Neomarphismui
          title="Death"
          color="neumorphic variation2 red"
          data={deaths}
          inc={
            props.deltas
              ? props.deltas.deceaseddelta >= 0
                ? "+" + props.deltas.deceaseddelta
                : props.deltas.deceaseddelta
              : ""
          }
        />
      </Col>
    </Row>
  );
};

export default Stats;
