import React, { PureComponent } from "react";
import {
  Brush,
  ResponsiveContainer,
  ComposedChart,
  Bar,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend
} from "recharts";

export default class RenderLineChart extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      data: props.timeseries,
      total: props.total,
      startIndex: 0,
      endIndex: 10
    };
  }
  componentDidMount() {
    let data = this.state.data;
    this.setState({
      startIndex: parseInt(data.length - 10),
      endIndex: parseInt(data.length - 1)
    });
  }
  render() {
    return (
      <div style={{ width: "100%", height: 400 }}>
        <ResponsiveContainer>
          <ComposedChart
            width={500}
            height={400}
            data={this.state.data}
            margin={{
              top: 20,
              right: 20,
              bottom: 20,
              left: -10
            }}
          >
            <CartesianGrid stroke="#f5f5f5" />
            <XAxis tick={{ fontSize: "14px" }} dataKey="date" />
            <YAxis
              tick={{ fontSize: "14px" }}
              domain={["auto", parseInt(this.state.total)]}
            />
            <Tooltip
              contentStyle={{ fontSize: "14px" }}
              formatter={(value, name, props) =>
                value + ", date: " + props.payload.date
              }
            />
            <Legend wrapperStyle={{ fontSize: "14px" }} />
            <Bar dataKey="dailyconfirmed" barSize={20} fill="#82ca9d" />
            <Line type="monotone" dataKey="totalconfirmed" stroke="#ff7300" />
            <Brush
              dataKey="date"
              height={30}
              stroke="#000000"
              startIndex={this.state.startIndex}
              endIndex={this.state.endIndex}
            >
              <ComposedChart>
                <Bar dataKey="dailyconfirmed" barSize={20} fill="#82ca9d" />
              </ComposedChart>
            </Brush>
          </ComposedChart>
        </ResponsiveContainer>
      </div>
    );
  }
}
